.loading {
  left: 0px;
  position: fixed;
  right: 0px;
  top: 0px;
  bottom: 0;
  width: 100%;
  z-index: 10000 !important;
  background: radial-gradient( rgba(107,0,120,1) 0%, rgba(0,0,0,1) 100%);
  display: flex;
  justify-content: center;
  transition: all ease-in-out 300ms;
}
.loading img {
  max-width: 80%;
  object-fit: contain;
}

.pulse {
  animation: pulse-animation 2s infinite ease-out;
}

@keyframes pulse-animation {
  0% {
      transform: scale(1);
  }
  50% {
      transform: scale(0.8);
  }
  100% {
      transform: scale(1);
  }
}
