.update-page {
    padding: 1rem;
    max-width: 600px;
    margin: 0 auto;
}

.update-container {
    background-color: #2a2a2a;
    border-radius: 12px;
    padding: 2rem;
    border: 1px solid #6b0078;
}

.update-title {
    text-align: center;
    color: #fff;
    margin-bottom: 2rem;
    font-size: 1.8rem;
}

.update-status {
    text-align: center;
    padding: 2rem;
    border-radius: 8px;
    margin-bottom: 1.5rem;
}

.update-status.checking {
    background-color: rgba(255, 255, 255, 0.05);
}

.update-status.available {
    background-color: rgba(0, 188, 212, 0.1);
    border: 1px solid rgba(0, 188, 212, 0.3);
}

.update-status.up-to-date {
    background-color: rgba(76, 175, 80, 0.1);
    border: 1px solid rgba(76, 175, 80, 0.3);
}

.update-status.error {
    background-color: rgba(244, 67, 54, 0.1);
    border: 1px solid rgba(244, 67, 54, 0.3);
}

.update-status h2 {
    color: #fff;
    margin: 1rem 0;
    font-size: 1.5rem;
}

.update-status p {
    color: #aaa;
    margin: 0;
}

.spinner {
    width: 40px;
    height: 40px;
    border: 3px solid rgba(255, 255, 255, 0.1);
    border-top-color: #6b0078;
    border-radius: 50%;
    animation: spin 1s linear infinite;
    margin: 0 auto 1rem;
}

@keyframes spin {
    to { transform: rotate(360deg); }
}

.update-badge {
    width: 60px;
    height: 60px;
    background-color: #4caf50;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto 1rem;
    animation: pulse 2s ease-in-out infinite;
}

@keyframes pulse {
    0%, 100% { transform: scale(1); }
    50% { transform: scale(1.1); }
}

.badge-icon {
    font-size: 2rem;
    font-weight: bold;
    color: #fff;
}

.checkmark {
    width: 60px;
    height: 60px;
    background-color: #4caf50;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto 1rem;
    font-size: 2rem;
    color: #fff;
}

.update-details {
    margin: 1.5rem 0;
    text-align: left;
    max-width: 300px;
    margin-left: auto;
    margin-right: auto;
}

.update-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.75rem;
    background-color: rgba(0, 0, 0, 0.2);
    border-radius: 6px;
    margin-bottom: 0.5rem;
    gap: 0.5rem;
}

@media (max-width: 400px) {
    .update-item {
        flex-direction: column;
        align-items: flex-start;
    }
}

.update-label {
    color: #aaa;
    font-weight: 500;
    flex-shrink: 0;
}

.update-versions {
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

.version-current {
    color: #999;
}

.version-arrow {
    color: #999;
    font-weight: bold;
}

.version-new {
    color: #4caf50;
    font-weight: bold;
}

.release-notes {
    background-color: rgba(0, 0, 0, 0.2);
    padding: 1rem;
    border-radius: 8px;
    margin: 1.5rem 0;
    text-align: left;
}

.release-notes h3 {
    color: #fff;
    margin: 0 0 0.5rem 0;
    font-size: 1rem;
}

.release-date {
    color: #999;
    font-size: 0.85rem;
    margin-bottom: 0.5rem;
    font-style: italic;
}

.release-notes-text {
    color: #4caf50;
    font-size: 1rem;
    line-height: 1.5;
    font-weight: 500;
}

.beta-toggle {
    margin: 1.5rem 0;
    padding: 1rem;
    background-color: rgba(0, 0, 0, 0.2);
    border-radius: 8px;
    text-align: left;
}

.beta-label {
    color: #fff;
    font-size: 0.95rem;
}

.beta-description {
    color: #999;
    font-size: 0.85rem;
}

.update-actions {
    text-align: center;
    margin-top: 1rem;
}

.install-button {
    padding: 12px 32px !important;
    font-size: 1.1rem !important;
    font-weight: bold !important;
    margin-bottom: 1rem !important;
}

.update-warning {
    color: #999;
    font-size: 0.85rem;
    margin-top: 1.5rem;
    padding: 1rem;
    background-color: rgba(0, 0, 0, 0.2);
    border-radius: 6px;
    text-align: center;
}

.current-versions {
    margin-top: 2rem;
    padding-top: 1.5rem;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.current-versions h3 {
    color: #fff;
    margin: 0 0 1rem 0;
    font-size: 1.1rem;
    text-align: center;
}

.version-info {
    display: flex;
    justify-content: space-between;
    padding: 0.5rem 0;
    max-width: 300px;
    margin: 0 auto;
}

.version-label {
    color: #999;
}

.version-value {
    color: #fff;
    font-weight: 500;
}

/* USB-only connectivity section */
.update-status.usb-only {
    background-color: rgba(0, 188, 212, 0.1);
    border: 1px solid rgba(0, 188, 212, 0.3);
}

.usb-icon {
    width: 60px;
    height: 60px;
    background-color: #00bcd4;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto 1rem;
    font-size: 1rem;
    font-weight: bold;
    color: #fff;
}

.usb-instructions {
    color: #ccc;
    margin-top: 1rem;
    font-size: 0.95rem;
}

.usb-link {
    display: inline-block;
    margin-top: 1rem;
    padding: 0.75rem 1.5rem;
    background-color: #00bcd4;
    color: #fff;
    text-decoration: none;
    border-radius: 6px;
    font-weight: 500;
    transition: background-color 0.2s ease;
}

.usb-link:hover {
    background-color: #0097a7;
}

/* Beta section */
.beta-section {
    margin-top: 2rem;
    padding: 1.5rem;
    background-color: rgba(156, 39, 176, 0.1);
    border: 1px solid rgba(156, 39, 176, 0.3);
    border-radius: 8px;
    text-align: center;
}

.beta-section h3 {
    color: #ce93d8;
    margin: 0 0 1rem 0;
    font-size: 1.2rem;
}

.beta-version-info {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    margin-bottom: 1rem;
}

.beta-version-info .beta-label {
    color: #999;
}

.beta-version {
    color: #ce93d8;
    font-weight: bold;
    font-size: 1.1rem;
}

.beta-description {
    color: #aaa;
    font-size: 0.9rem;
    margin-bottom: 1rem;
}

.beta-button {
    background-color: #9c27b0 !important;
    color: #fff !important;
    padding: 10px 24px !important;
    font-weight: 500 !important;
}

.beta-button:hover {
    background-color: #7b1fa2 !important;
}

.beta-button:disabled {
    background-color: #666 !important;
    color: #999 !important;
}
