.log-viewer-container {
  padding: 1.5rem;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  height: calc(100vh - 120px);
}

.log-header {
  margin-bottom: 1rem;
}

.log-header h2 {
  margin: 0 0 0.25rem 0;
  color: #fff;
}

.log-subtitle {
  color: #fff;
  font-size: 0.9rem;
}

.log-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  flex-wrap: wrap;
  gap: 1rem;
}

.log-checkboxes {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  flex-wrap: wrap;
}

.auto-scroll-toggle {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
  color: #fff;
  font-size: 0.95rem;
}

.auto-scroll-toggle input {
  width: 18px;
  height: 18px;
  cursor: pointer;
}

.log-buttons {
  display: flex;
  gap: 0.75rem;
}

.log-content-wrapper {
  flex: 1;
  background-color: #1e1e1e;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
}

.log-content {
  margin: 0;
  padding: 1rem;
  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', 'Consolas', monospace;
  font-size: 0.85rem;
  line-height: 1.5;
  color: #d4d4d4;
  white-space: pre-wrap;
  word-wrap: break-word;
  overflow-y: auto;
  height: 100%;
  max-height: calc(100vh - 280px);
}

/* Scrollbar styling for log content */
.log-content::-webkit-scrollbar {
  width: 10px;
}

.log-content::-webkit-scrollbar-track {
  background: #2d2d2d;
}

.log-content::-webkit-scrollbar-thumb {
  background: #555;
  border-radius: 5px;
}

.log-content::-webkit-scrollbar-thumb:hover {
  background: #777;
}

@media (max-width: 600px) {
  .log-viewer-container {
    padding: 1rem;
  }

  .log-controls {
    flex-direction: column;
    align-items: flex-start;
  }

  .log-content {
    font-size: 0.75rem;
    max-height: calc(100vh - 320px);
  }
}
