.upload-artwork-container {
    padding: 20px;
    max-width: 800px;
    margin: 0 auto;
}

.upload-header h2 {
    color: #fff;
    margin-bottom: 20px;
}

.upload-instructions {
    background: rgba(255, 255, 255, 0.05);
    border-radius: 8px;
    padding: 16px;
    margin-bottom: 20px;
    border: 1px solid rgba(255, 255, 255, 0.1);
}

.upload-instructions p {
    color: #fff;
    margin-bottom: 10px;
}

.upload-instructions ul {
    color: #d1d5db;
    margin: 0;
    padding-left: 20px;
}

.upload-instructions li {
    margin-bottom: 6px;
    line-height: 1.5;
}

.upload-instructions code {
    background: rgba(0, 0, 0, 0.3);
    padding: 2px 6px;
    border-radius: 4px;
    font-family: monospace;
    color: #10b981;
}

.upload-storage-status {
    margin-bottom: 20px;
}

.storage-indicator {
    display: inline-block;
    padding: 8px 16px;
    border-radius: 6px;
    font-size: 14px;
    font-weight: 500;
}

.storage-indicator.usb-active {
    background: rgba(34, 197, 94, 0.2);
    color: #22c55e;
    border: 1px solid rgba(34, 197, 94, 0.3);
}

.storage-indicator.internal-active {
    background: rgba(59, 130, 246, 0.2);
    color: #3b82f6;
    border: 1px solid rgba(59, 130, 246, 0.3);
}

.upload-options {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-bottom: 24px;
}

.option-group {
    background: rgba(255, 255, 255, 0.03);
    border-radius: 8px;
    padding: 16px;
    border: 1px solid rgba(255, 255, 255, 0.1);
}

.option-group h3 {
    color: #fff;
    font-size: 16px;
    margin-bottom: 12px;
}

.option-hint {
    color: #9ca3af;
    font-size: 13px;
    margin-bottom: 12px;
}

.system-select {
    width: 100%;
    min-width: 300px;
    margin-bottom: 16px;
}

.system-select .MuiOutlinedInput-root {
    color: #fff;
}

.system-select .MuiOutlinedInput-notchedOutline {
    border-color: rgba(255, 255, 255, 0.3);
}

.system-select .MuiInputLabel-root {
    color: rgba(255, 255, 255, 0.7);
}

.system-select .MuiSelect-icon {
    color: rgba(255, 255, 255, 0.7);
}

.menu-divider {
    font-size: 12px !important;
    color: #9ca3af !important;
    text-align: center;
}

.add-system-section {
    margin-top: 12px;
}

.new-system-input {
    display: flex;
    gap: 8px;
    align-items: center;
}

.new-system-input .MuiTextField-root {
    flex: 1;
}

.new-system-input .MuiOutlinedInput-root {
    color: #fff;
}

.new-system-input .MuiOutlinedInput-notchedOutline {
    border-color: rgba(255, 255, 255, 0.3);
}

.upload-dropzone {
    border: 2px dashed rgba(255, 255, 255, 0.3);
    border-radius: 12px;
    padding: 48px 24px;
    text-align: center;
    cursor: pointer;
    transition: all 0.2s ease;
    position: relative;
    background: rgba(255, 255, 255, 0.02);
}

.upload-dropzone:hover {
    border-color: rgba(124, 58, 237, 0.6);
    background: rgba(124, 58, 237, 0.05);
}

.upload-dropzone.drag-active {
    border-color: #7c3aed;
    background: rgba(124, 58, 237, 0.1);
    transform: scale(1.01);
}

.upload-dropzone.uploading {
    opacity: 0.6;
    pointer-events: none;
}

.upload-icon {
    font-size: 64px !important;
    color: rgba(255, 255, 255, 0.4);
    margin-bottom: 16px;
}

.dropzone-text {
    color: #fff;
    font-size: 18px;
    margin-bottom: 8px;
}

.dropzone-hint {
    color: #9ca3af;
    font-size: 14px;
}

.file-input {
    position: absolute;
    inset: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
}

.upload-results {
    margin-top: 24px;
    background: rgba(255, 255, 255, 0.03);
    border-radius: 8px;
    padding: 16px;
    border: 1px solid rgba(255, 255, 255, 0.1);
}

.upload-results h3 {
    color: #fff;
    font-size: 16px;
    margin-bottom: 12px;
}

.results-list {
    max-height: 200px;
    overflow-y: auto;
    margin-bottom: 12px;
}

.result-item {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 8px;
    border-radius: 4px;
    margin-bottom: 4px;
}

.result-item.success {
    background: rgba(34, 197, 94, 0.1);
}

.result-item.error {
    background: rgba(239, 68, 68, 0.1);
}

.result-icon {
    font-weight: bold;
    width: 20px;
    text-align: center;
}

.result-item.success .result-icon {
    color: #22c55e;
}

.result-item.error .result-icon {
    color: #ef4444;
}

.result-name {
    color: #fff;
    font-family: monospace;
    font-size: 13px;
}

.result-error {
    color: #f87171;
    font-size: 12px;
    margin-left: auto;
}

.result-info {
    color: #60a5fa;
    font-size: 12px;
    margin-left: auto;
    font-style: italic;
}

/* Radio button styling */
.MuiRadio-colorPrimary.Mui-checked {
    color: #7c3aed !important;
}

.MuiFormControlLabel-label {
    color: #fff !important;
}
