body {
    min-height: 100vh;
    /* background: linear-gradient(0deg, rgba(0,0,001) 0%, rgba(107,0,120,1) 100%); */
    background: radial-gradient( rgba(107,0,120,1) 0%, rgba(0,0,0,1) 100%);
    color: #eaeaea;
}
.device-item {
    border-radius: 0.5rem;
    border: solid 2px #6b0078;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.flex {
    display: flex;
}
.flex-between {
    display: flex;
    justify-content: space-between;
}
.flex-grow {
    flex-grow: 1;
}
.flex-column {
    display: flex;
    flex-direction: column;
}
.layout-grid {
    display: grid;
    grid-gap: 0.5rem 1rem;
    padding: 0.5rem;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}
.layout-grid-marquee {
    display: grid;
    grid-gap: 0.5rem 1rem;
    padding: 0.5rem;
    grid-template-columns: repeat(auto-fit, minmax(300px, 358px));
    justify-content: center;
}

.grid-item {
    border-radius: 4px;
    width: 100%;
    max-height: 60px;
    display: flex;
    align-items: center;
    border: 1px solid #6b0078;
    color: #fff;
    box-sizing: border-box;
    transition: all 450ms ease;
    object-fit: contain;
    position: relative;
}
.nav-element {
    background-color: #4c3ba2;
    padding: 0.5rem;
    justify-content: center;
    cursor: pointer;
    min-height: 60px;
}
.fast-fade {
    animation: fadeIn 300ms;
  }
.fade-in {
    animation: fadeIn 1s;
  }
  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  .settings {
    justify-content: space-between;
    border-radius: 4px;
    width: 100%;
    display: flex;
    flex-direction: column;
    color: #000;
    background-color: #FFFFFF;
    box-sizing: border-box;
    transition: all 450ms ease;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
    padding: 1rem;
    font-weight: 600;
  }
.rrs {
    z-index: 10000;
}
.react-colorful {
    width: auto !important;
}

/* Hide logo on mobile (iOS app has its own header) */
@media (max-width: 600px) {
    .logo-container {
        display: none;
    }

    /* Single column for marquee list on mobile */
    .layout-grid {
        grid-template-columns: 1fr;
        grid-gap: 0.25rem;
        padding: 0.25rem;
    }

    /* 2-column layout for console/system list on mobile */
    .layout-grid-consoles {
        grid-template-columns: repeat(2, 1fr);
    }
}
