/* Widget Settings Page - Purple Theme */

.widget-settings-page {
    background: linear-gradient(180deg, #2d1f3d 0%, #1a1025 100%);
    min-height: 100vh;
    color: #fff;
}

.widget-header {
    display: flex;
    align-items: center;
    padding: 1rem;
    border-bottom: 1px solid #6b0078;
}

.back-button {
    cursor: pointer;
    display: flex;
    align-items: center;
    color: #fff;
}

.back-button:hover {
    color: #bb86fc;
}

.widget-title {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1rem;
    font-size: 1.5rem;
    font-weight: bold;
    color: #fff;
}

.widget-content {
    padding: 1rem;
}

.widget-section {
    background-color: #3b153f;
    border: solid 1px #6b0078;
    border-radius: 0.5rem;
    padding: 1.5rem;
    margin-bottom: 1rem;
}

.section-title {
    margin-bottom: 1rem;
    color: #fff;
    font-size: 1.2rem;
    font-weight: 500;
}

/* Dropdown row for crypto/stock selectors */
.dropdown-row {
    display: flex;
    gap: 1rem;
    margin-bottom: 1rem;
}

.dropdown-item {
    flex: 1;
}

/* Input row */
.input-row {
    display: flex;
    gap: 0.5rem;
    margin-bottom: 1rem;
    align-items: center;
    padding: 0 0.5rem;
}

.symbol-input {
    flex: 1;
    max-width: 65%;
    height: 40px;
    font-size: 1rem;
    padding: 0 1rem;
    border-radius: 4px;
    border: 1px solid #6b0078;
    background-color: #2d1f3d;
    color: #fff;
    outline: none;
}

@media (max-width: 500px) {
    .symbol-input {
        max-width: 60%;
        padding: 0 0.75rem;
    }
}

.symbol-input:focus {
    border-color: #bb86fc;
    box-shadow: 0 0 0 2px rgba(187, 134, 252, 0.25);
}

.symbol-input::placeholder {
    color: #888;
}

.add-button {
    background-color: #4169E1 !important;
    color: #fff !important;
    height: 40px;
    min-width: 80px !important;
    flex-shrink: 0;
}

.add-button:hover {
    background-color: #5a7fe8 !important;
}

@media (max-width: 500px) {
    .add-button {
        min-width: 70px !important;
        padding: 0 0.75rem !important;
    }
}

/* Symbol chips */
.symbol-chips {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
    margin-bottom: 1.5rem;
}

.symbol-chip {
    display: flex;
    align-items: center;
    background-color: transparent;
    border-radius: 20px;
    padding: 0.4rem 0.8rem;
    color: #fff;
    font-size: 0.9rem;
    border: 2px solid;
}

/* Stock chips - purple border */
.symbol-chip.stock {
    border-color: #6b0078;
}

/* Crypto chips - blue border */
.symbol-chip.crypto {
    border-color: #1976d2;
}

.chip-close {
    margin-left: 0.5rem;
    font-size: 1rem !important;
    cursor: pointer;
    opacity: 0.8;
}

.chip-close:hover {
    opacity: 1;
}

/* Settings grid */
.settings-grid {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 1rem;
    margin-bottom: 1.5rem;
}

@media (max-width: 900px) {
    .settings-grid {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 500px) {
    .settings-grid {
        grid-template-columns: 1fr;
    }

    .dropdown-row {
        flex-direction: column;
    }
}

.setting-item {
    display: flex;
    flex-direction: column;
}

.setting-label {
    display: block;
    margin-bottom: 0.5rem;
    color: #fff;
    font-size: 0.9rem;
}

/* Setting row for full-width dropdowns */
.setting-row {
    margin-bottom: 1rem;
}

/* Start/Stop button */
.start-button {
    background-color: #4169E1 !important;
    color: #fff !important;
    padding: 0.75rem 2rem !important;
    font-size: 1rem !important;
    display: flex !important;
    align-items: center !important;
    gap: 0.5rem !important;
}

.start-button:hover {
    background-color: #5a7fe8 !important;
}

.start-button.stop {
    background-color: #d32f2f !important;
}

.start-button.stop:hover {
    background-color: #f44336 !important;
}

.start-button:disabled {
    background-color: #555 !important;
    color: #999 !important;
}

.start-button.stop:disabled {
    background-color: #d32f2f !important;
    color: #fff !important;
}

/* Override react-responsive-select styles for purple theme */
.rrs {
    position: relative;
    z-index: 10;
}

.rrs--options-visible {
    z-index: 100;
}

.rrs__button {
    background-color: #3b153f !important;
    border: 1px solid #6b0078 !important;
    color: #fff !important;
    border-radius: 4px !important;
}

.rrs__button:focus {
    border-color: #bb86fc !important;
    box-shadow: 0 0 0 2px rgba(187, 134, 252, 0.25) !important;
}

.rrs__label {
    color: #fff !important;
}

.rrs__options {
    background-color: #3b153f !important;
    border: 1px solid #6b0078 !important;
    border-radius: 4px !important;
}

.rrs__option {
    color: #fff !important;
    background-color: #3b153f !important;
    padding: 10px 12px !important;
}

.rrs__option:hover {
    background-color: #4c3ba2 !important;
}

.rrs__option.rrs__option--is-focused {
    background-color: #4c3ba2 !important;
}

.rrs__option.rrs__option--is-selected {
    background-color: #6b0078 !important;
}

/* Caret icon color */
.rrs__button .caret-icon {
    fill: #fff !important;
}

/* Modal styles for mobile */
.rrs--options-visible .rrs__options {
    max-height: 300px;
    overflow-y: auto;
}

/* News source chips container */
.chip-container {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
    margin-bottom: 0.5rem;
}

.source-chip {
    display: inline-flex;
    align-items: center;
    background-color: transparent;
    border: 2px solid #6b0078;
    border-radius: 20px;
    padding: 0.4rem 0.8rem;
    color: #fff;
    font-size: 0.85rem;
}

.source-chip span {
    margin-right: 0.5rem;
}

.chip-remove {
    display: flex;
    align-items: center;
    justify-content: center;
    background: none;
    border: none;
    color: #fff;
    cursor: pointer;
    padding: 0;
    opacity: 0.7;
    transition: opacity 0.2s;
}

.chip-remove:hover {
    opacity: 1;
}

/* Loading button state */
.start-button.loading {
    background-color: #666 !important;
    cursor: wait !important;
}

/* Spinner animation */
.spinner {
    display: inline-block;
    width: 16px;
    height: 16px;
    border: 2px solid rgba(255, 255, 255, 0.3);
    border-radius: 50%;
    border-top-color: #fff;
    animation: spin 1s linear infinite;
    margin-right: 8px;
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}
