/* Widgets Menu Page */
.widgets-menu {
    padding: 1rem;
}

.widgets-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
    max-width: 600px;
    margin: 0 auto;
}

@media (max-width: 500px) {
    .widgets-grid {
        grid-template-columns: repeat(2, 1fr);
    }
}

.widget-button {
    display: flex !important;
    flex-direction: column !important;
    align-items: center !important;
    justify-content: center !important;
    padding: 1.5rem 1rem !important;
    min-height: 100px !important;
    background-color: #4c3ba2 !important;
    border: 1px solid #6b0078 !important;
    border-radius: 8px !important;
    color: #fff !important;
    transition: all 0.2s ease !important;
}

.widget-button:hover {
    background-color: #5d4bb3 !important;
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba(107, 0, 120, 0.4);
}

.widget-button.active {
    background-color: #d32f2f !important;
    border-color: #f44336 !important;
}

.widget-button.active:hover {
    background-color: #e53935 !important;
}

.widget-button.settings-button {
    background-color: #3b153f !important;
    border: 1px solid #6b0078 !important;
}

.widget-button.settings-button:hover {
    background-color: #4c3ba2 !important;
}

.widget-icon {
    font-size: 2.5rem !important;
    margin-bottom: 0.5rem !important;
}

.widget-label {
    font-size: 0.9rem;
    font-weight: 500;
    text-transform: none;
}

/* Disabled state */
.widget-button:disabled {
    background-color: #555 !important;
    opacity: 0.7;
}
